import React, { useState } from "react";
import Team from "../Team";
import PlaceHolderTeam from "../../util/PlaceHolderTeam";
import "./Match.css";

const Match = (props) => {
  const [match, setMatch] = useState(props.match);
  const selectedHandler = (team) => {
    const updatedMatch = { ...match };
    if (team.position === updatedMatch.team1.position) {
      if (updatedMatch.team1.selected || !updatedMatch.team2.selected) {
        team.selected = !team.selected ? true : false;
      }
      updatedMatch.team1 = team;
    } else {
      if (updatedMatch.team2.selected || !updatedMatch.team1.selected) {
        team.selected = !team.selected ? true : false;
      }
      updatedMatch.team2 = team;
    }
    setMatch(updatedMatch);
  };

  const team1 =
    props.match && props.match.team1 ? props.match.team1 : PlaceHolderTeam;
  const team2 =
    props.match && props.match.team2 ? props.match.team2 : PlaceHolderTeam;

  return (
    <div className="match">
      <div className="teams border border-slate-300 rounded-tl-3xl rounded-br-3xl shadow-xl">
        <Team team={team1} onSelected={selectedHandler} size="small" />
        <Team team={team2} onSelected={selectedHandler} size="small" />
      </div>
    </div>
  );
};

export default Match;
