import Section from "./Section";

const Round = (props) => {
  const sections = [];
  for (let i = 0; i < props.matchups.length; i = i + 2) {
    const matchups = [];
    if (props.matchups.length === 1) {
      matchups.push(props.matchups[i]);
    } else {
      matchups.push(props.matchups[i]);
      matchups.push(props.matchups[i + 1]);
    }
    sections.push(matchups);
  }
  return (
    <div
      className={`round ${props.class} w-full shrink-0 border-dashed border-r border-slate-100 px-2`}
    >
      <div className="text-xl md:text-2xl font-bold mb-2 text-slate-600">
        {props.name}
      </div>
      {sections.map((section, i) => (
        <Section matchups={section} key={i} />
      ))}
    </div>
  );
};

export default Round;
